import React, { Component } from 'react';
import { Field, reduxForm } from 'redux-form'

import TextField from '@material-ui/core/TextField'
import Checkbox from '@material-ui/core/Checkbox'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import Select from '@material-ui/core/Select'
import InputLabel from '@material-ui/core/InputLabel'
import FormHelperText from '@material-ui/core/FormHelperText'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'


// actions

const renderFromHelper = ({ touched, error }) => {
  if (!(touched && error)) {
    return
  } else {
    return <FormHelperText>{touched && error}</FormHelperText>
  }
}

const renderTextField = ({
  label,
  placeholder,
  input,
  multiline,
  meta: { touched, invalid, error },
  labelClass,
  ...custom
}) => (
  <TextField
    label={label}
    placeholder={placeholder}
    error={touched && invalid}
    helperText={touched && error}
    {...input}
    {...custom}
    InputLabelProps={{
      className: labelClass,
    }}
  />
)

const renderSelectField = ({
  input,
  label,
  formClass,
  labelClass,
  meta: { touched, error },
  children,
  ...custom
}) => (
  <FormControl error={touched && error} className={formClass}>
    <InputLabel className={labelClass}>{label}</InputLabel>
    <Select
      native
      {...input}
      {...custom}
    >
      {children}
    </Select>
    {renderFromHelper({ touched, error })}
  </FormControl>
)

const renderDateTimeField = ({
  label,
  placeholder,
  input,
  meta: { touched, invalid, error },
  labelClass,
  ...custom
}) => (

  <TextField
    label={label}
    type="date"
    error={touched && invalid}
    helperText={touched && error}
    {...input}
    {...custom}
    InputLabelProps={{
      className: labelClass,
      shrink: true 
    }}
  />
)

const renderField = ({
  input,
  label,
  placeholder,
  type,
  required,
  meta: { touched, error, warning }
}) => (
  <div className="input-style input-style-2">
    {label && <span className="color-highlight">{label} </span>}
    {required && <em>*</em>}
    <input {...input} placeholder={placeholder} type={type}/>
    {touched &&
      ((error && <span>{error}</span>) ||
        (warning && <span>{warning}</span>))}
  </div>
)

function JobForm2 (props) {
  let { handleSubmit, pristine, reset, submitting, categories, job, jobDate } = props
  return (
    <div>
        <Field
          component={renderTextField}
          name="title"
          // type="text"
          // placeholder='  ?'
          label='  ?'
          // required={true}
          className='input-group my-2'
          labelClass='font-12'
        />

        <Field 
          name="category" 
          component={renderSelectField}
          labelClass='font-12 '
          formClass='input-group my-2'
          label='  '
          required={true}>
            {categories && categories.length > 0 && categories.map(category => 
              <option className='font-12' value={category.id} key={category.id}>{category.ru_name}</option>
              )}
        </Field>
          
        <Field name="description" component="textarea" className="form-control font-12 my-2"
          style={{"height": "150px"}} label='    '
          placeholder='   ? 
          -   .
          -      ,       .
          -     ,   .
          '
          />
        <FormControl className='input-group my-2'>
          <InputLabel className='font-12'>   ?</InputLabel>
          <Select onChange={props.setData} name='jobDate' className='input-group font-12'>
            <option value={'any'}>  </option>
            <option value={'period'}> </option>
            <option value={'not later'}> , </option>
          </Select>
        </FormControl>
        {jobDate == 'period' && 
          <div>
            <Field
              name="start_date"
              component={renderDateTimeField}
              label="C"
            />
            <Field
              name="end_date"
              component={renderDateTimeField}
              label=""
            />
          </div>
          }
        {jobDate == 'not later' && 
          <Field
            name="end_date"
            component={renderDateTimeField}
          />
        }
        
        <Field
          name="budget"
          component={renderTextField}
          type="number"
          label=" "
          className='input-group my-2'
          labelClass='font-12'
        />

        <Field
          component={renderTextField}
          name="address"
          type="text"
          value={'-'}
          label=''
          className='input-group my-2'
          labelClass='font-12'
        />
        
    </div>
  )
}

export class JobFirstCreateForm extends Component {
  constructor(props) {
   super(props);
   this.state = {
     jobDate: 'any',
   };

   this.setData = this.setData.bind(this);   
 }

  setData (e) {
    this.setState({
      [e.target.name]: e.target.value
      })
  }
  
  render () {
    let { handleSubmit, pristine, reset, submitting, user, categories } = this.props
    return (
      <form onSubmit={handleSubmit}>
        <div>
          <Field
            component={renderField}
            name="name"
            type="text"
            placeholder={''}
            value={user ? user.nickname : ''}
            defaultValue={user ? user.nickname : ''}
            label='   ?'
          />
        </div>
        <div>
          <Field
            name="phone"
            component={renderField}
            type="number"
            placeholder="79148965232"
            label=""
          />
        </div>
        <JobForm2 
          handleSubmit={handleSubmit}
          pristine={pristine}
          reset={reset}
          submitting={submitting}
          categories={categories}
          setData={this.setData}
          job={null}
          jobDate={this.state.jobDate} />
        
        <div>
          <button className='btn btn-primary'
            // type="submit"
            onClick={handleSubmit}

            disabled={pristine || submitting}>
            
          </button>
        </div>
      </form>
    )
  }
}

JobFirstCreateForm = reduxForm({
  form: 'jobFirstCreate' // a unique identifier for this form
})(JobFirstCreateForm)


export class JobCreateForm extends Component {
  constructor(props) {
   super(props);
   this.state = {
     jobDate: 'any',
   };

   this.setData = this.setData.bind(this);   
 }

 componentDidMount() {
  const { start_date, end_date } = this.props.initialValues
  if (start_date) {
    this.setState({...this.state, jobDate : 'period'})
  }
  if (end_date && !start_date){
    this.setState({...this.state, jobDate : 'not later'})
  }
}

  setData (e) {
    this.setState({
      [e.target.name]: e.target.value
      })
  }
  
  render () {
    let { handleSubmit, pristine, reset, submitting, categories, job } = this.props
    return (
      <div>
        <JobForm2 
          handleSubmit={handleSubmit}
          pristine={pristine}
          reset={reset}
          submitting={submitting}
          categories={categories}
          setData={this.setData}
          job={job}
          jobDate={this.state.jobDate} />
        <div>
          <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
              bg-blue2-light text-wrap'
            onClick={handleSubmit}
            disabled={pristine || submitting}>
            
          </button>
        </div>
      </div>
    )
  }
}

JobCreateForm = reduxForm({
  form: 'jobCreate', // a unique identifier for this form
})(JobCreateForm)


