export function UsernamePhoneForm (props) {
  const { edit, setEdit, handleSubmit } = props
  function clickSave() {
    setEdit(!edit);
    handleSubmit()
  }
  return (
    <div>
      <Field
          component={renderTextField}
          name="nickname"
          label='   ?'
          className='input-group my-2'
          labelClass='font-12'
        />
      <Field
          component={renderTextField}
          name="phone"
          label=''
          className='input-group my-2'
          labelClass='font-12'
        />
        <Field
          component={renderTextField}
          name="id"
          hidden={true}
        />
      <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
          bg-blue2-light text-wrap'
        onClick={clickSave}
        disabled={props.pristine || props.submitting}>
        
      </button>
      <button className='btn btn-m mb-1 rounded-s text-uppercase font-900 shadow-s
          bg-blue2-light text-wrap float-right'
        onClick={() => setEdit(!edit)}
      >
        
      </button>
    </div>
  )
}

UsernamePhoneForm = reduxForm({
  form: 'usernamePhoneForm', // a unique identifier for this form
})(UsernamePhoneForm)